<template>
  <div class="content">
    <Row>
      <Col span="5">
        <Card class="card_con">
          <template #title>数据采集表单</template>
          <ul>
            <li v-for="(item, index) in tableList" :key="item.pk_id" :class="{tablelist:true,active:activeList==index}" @click="tabChange(item,index)">
              {{item.index_item}}
            </li>
          </ul>
        </Card>
      </Col>
      <Col span="14" style="padding:0 10px">
        <Card class="card_con">
          <template #title>表单字段</template>
          <EditTableField :SonTableListId="SonTableListId"></EditTableField>
        </Card>
      </Col>
      <Col span="5">
        <Card class="card_con">
          <template #title>调查表介绍</template>
          <div class="introduce">
            <textarea  v-model="item.remark" placeholder="调查表介绍" > </textarea>
          </div>
          <p class="introduce_btn">
            <Button type="primary" @click="updateSonTable" style="margin-left: 20px">保存</Button>
          </p>
        </Card>
      </Col>
    </Row>
  </div>
</template>

<script>
import { getSonTableList, updateSonTable } from '@/api/dataCollection'
import EditTableField from './EditTable_filed'
export default {
  name: 'editTable',
  data () {
    return {
      tableList: [], // 存储信息调查表的列表数据
      activeList: 0,
      /* 添加字段框状态 */
      addFieldModel: false,
      /* 调查表id */
      SonTableListId: 1,
      /* 选中的调查表信息 */
      item: {}
    }
  },
  components: {
    EditTableField
  },
  created () {
    /* 获取数据调查的表格 */
    this.getSonTableList()
  },
  mounted () {
  },
  methods: {
    /* 获取数据调查的表格 */
    getSonTableList () {
      getSonTableList({
        'data': {
          pageSize: 20,
          pageNumber: 1,
          tableName: ''
        }
      }).then(res => {
        if (res.data.code === '0000') {
          this.tableList = res.data.data.rows
          this.item = this.tableList[0]
          this.SonTableListId = this.item.pk_id
        }
      })
    },
    /* 修改调查表介绍 */
    updateSonTable () {
      updateSonTable({
        data: {
          tableName: this.item.index_item,
          pkId: this.item.pk_id,
          remark: this.item.remark
        }
      }).then(res => {
        if (res.data.code === '0000') {
          this.$Notice.success({ title: '保存成功' })
        } else {
          this.$Notice.error({ title: res.data.msg })
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 出发添加字段 */
    saveFiled () {
      this.$refs.addField.saveFiled()
    },
    /* 切换表 */
    tabChange (item, index) {
      this.item = item
      this.activeList = index
      this.SonTableListId = item.pk_id
    }
  }
}
</script>

<style scoped lang="scss">
.content{
  padding: 15px;
  .card_con{
    height: calc(100vh - 215px);
  }
  .tablelist{
    line-height: 40px;
    &:hover{
      background: #f6f6f6;
      cursor: pointer;
    }
    &.active{
      background: #57a3f3;
      color: white;
    }
  }
  .introduce{
    textarea{
      height:calc(100vh - 350px);
      width: 100%;
      border-radius: 5px;
      outline: none;
      padding: 10px;
      border: 1px solid #eee;
      resize:none
    }
  }
  .introduce_btn{
    text-align: right;
    margin: 10px 20px 0;
    position: absolute;
    bottom: 25px;
    right: 0
  }
}
</style>
