<template>
  <div class="editTable">
    <draggable v-model="fieldlist" group="people" @start="drag=true" @end="drag=false" @change="fieldDataChange">
      <div v-for="(element,index) in fieldlist" :key="element.sort" class="li_box">
              <span class="from_lable">
                {{element.filedName}}
                <i v-show="element.isMust == '1'">*</i>
              </span>
        <Input :placeholder="element.filedName" style="width: 250px" v-if="element.filedType == 1"/>
        <Input :placeholder="element.filedName" :max="element.max === null ? Infinity : element.max * 1" :min="element.min === null ? -Infinity : element.min * 1" style="width: 250px" v-if="element.filedType == 2" :active-change="false"/>
        <Input :placeholder="element.filedName" style="width: 250px" v-if="element.filedType == 3"/>
        <Select style="width:250px" v-if="element.filedType == 4">
          <Option v-for="(item, index) in element.selects" :key="element.sort.toString() + index" :value="index">
            {{item}}
          </Option>
        </Select>
        <Select multiple style="width:250px" v-if="element.filedType == 5">
          <Option v-for="(item, index) in element.selects" :key="element.sort.toString() + index" :value="index">
            {{item}}
          </Option>
        </Select>
        <RadioGroup v-if="element.filedType == 6">
          <Radio  v-for="(item, index) in element.selects" :key="element.sort.toString() + index" :label="item" :value="index" style="height: 30px;line-height: 30px;">
          </Radio>
        </RadioGroup>
        <DatePicker type="date" :placeholder="element.filedName" style="width: 250px" v-if="element.filedType == 7"></DatePicker>
        <span class="dttails_btn" @click="filedDetails = filedDetails == element.sort ? -1 :element.sort">
          详情<Icon type="md-arrow-dropdown"/>
        </span>
        <span class="dttails_btn" @click="delField(index,element.id)" style="margin-right: 15px">
          删除
        </span>
        <div v-show="filedDetails == element.sort" class="details_box">
          <span style="margin-right: 15px;">是否必填：{{element.isMust == 1 ? '是': '否' }}</span>
          <span style="margin-right: 15px;" v-if="element.filedType == 1 && element.stringLength !== null">长度：{{element.stringLength}}</span>
          <span style="margin-right: 15px;" v-if="(element.filedType == 2 || element.filedType == 3)&& element.max !== null">最大值：{{element.max}}</span>
          <span style="margin-right: 15px;" v-if="(element.filedType == 2 || element.filedType == 3)&& element.min !== null">最小值：{{element.min}}</span>
          <span style="margin-right: 15px;" v-if="(element.filedType == 3)&& element.max !== null">保留小数位：{{element.decimalPoint}}</span>
        </div>
        <!--{{element.sort}}-->
      </div>
    </draggable>
    <p style="text-align:right;margin:10px 20px 0;position: absolute;bottom:25px;right:20px;">
      <Button type="primary" @click="$refs.addField.addFieldModel = true">添加字段</Button>
      <Button type="primary" @click="updataFieldList" :loading="loading" style="margin-left: 20px">保存</Button>
    </p>
    <EditTableAddField ref="addField"  @addArr="addArr"></EditTableAddField>
  </div>
</template>

<script>
import { getFileds, save } from '@/api/dataCollection'
import EditTableAddField from './EditTable_addField'
import draggable from 'vuedraggable'
export default {
  name: 'editTable',
  data () {
    return {
      tableList: [], // 存储信息调查表的列表数据
      /* 添加字段框状态 */
      addFieldModel: false,
      /* file详情打开状态 */
      filedDetails: false,
      /* 删除的字段的id */
      idArr: [],
      /* 保存表单loading */
      loading: false,
      fieldlist: []
    }
  },
  watch: {
    SonTableListId () {
      this.getFileds()
    }
  },
  props: {
    SonTableListId: {}
  },
  components: {
    EditTableAddField,
    draggable
  },
  methods: {
    /* 接口：获取字段列表 */
    getFileds () {
      getFileds({
        'data': {
          id: this.SonTableListId
        }
      }).then(res => {
        if (res.data.code === '0000') {
          if (res.data.data === null) {
            this.fieldlist = []
          } else {
            this.fieldlist = res.data.data
          }
        }
      })
    },
    /* 修改排序字段 */
    save (data) {
      let p = new Promise(
        resolve => {
          save({
            'data': data
          }).then(res => {
            if (res.data.code === '0000') {
              resolve('ok')
              this.loading = false
              this.$Notice.success({ title: '保存成功' })
            } else {
              this.$Notice.error({ title: res.data.msg })
            }
          }).catch(() => {
            this.$Notice.error({ title: '服务器错误' })
          })
        }
      )
      return p
    },
    /* 返回修改后的字段结果 */
    fieldDataChange () {
      for (let i in this.fieldlist) {
        this.fieldlist[i].sort = i * 1 + 1
      }
    },
    /* 将添加组件的结果添加进this.fieldlist */
    addArr (formItem) {
      this.fieldlist.push(formItem)
      this.fieldDataChange()
    },
    /* 删除字段 */
    delField (index, id) {
      if (id !== undefined) {
        this.idArr.push(id)
      }
      this.fieldlist.splice(index, 1)
      this.fieldDataChange()
    },
    /* 发布提交 */
    updataFieldList () {
      this.loading = true
      let savaData = {
        delete: [],
        add: [],
        update: []
      }
      /* 添加字段 */
      for (let i in this.fieldlist) {
        this.fieldlist[i].indexItemId = this.SonTableListId
        if (this.fieldlist[i].id === undefined) {
          savaData.add.push(this.fieldlist[i])
        } else {
          savaData.update.push(this.fieldlist[i])
        }
      }
      savaData.delete = this.idArr

      this.save(savaData).then(() => {
        this.getFileds()
        this.idArr = []
      })
    }
  }
}
</script>

<style scoped lang="scss">
.editTable{
  height:calc(100vh - 350px);
  overflow-y:auto;
  .li_box{
    border: 1px solid #d1d1d1;
    padding: 10px 20px;
    margin: 5px;
    text-align: left;
    .from_lable{
      display: inline-block;width: 160px;
      i{
        color: red;
      }
    }
    .dttails_btn{
      float: right;line-height:35px;font-size: 12px;color:#1BA9F5;cursor: pointer
    }
    .details_box{
      margin-top:15px;
      padding: 15px 15px 0;
      border-top: 1px solid #eee;
    }
  }
}
</style>
