<template>
  <div class="content">
    <Modal v-model="addFieldModel" title="添加字段">
      <Form :model="formItem"  :rules="loginRules"  ref="FormRef" :label-width="125">
        <FormItem label="字段名称"  prop="filedName">
          <Input v-model.trim="formItem.filedName" placeholder="请输入字段名称"></Input>
        </FormItem>
        <FormItem label="字段类型">
          <Select v-model="formItem.filedType" @on-change="delAddValue">
            <Option value="1">文本</Option>
            <Option value="2">整数</Option>
            <Option value="3">小数</Option>
            <Option value="4">单选下拉框</Option>
            <Option value="5">多选下拉框</Option>
            <Option value="6">勾选框</Option>
            <Option value="7">时间框</Option>
          </Select>
        </FormItem>
        <FormItem label="是否必填">
          <RadioGroup v-model="formItem.isMust">
            <Radio label="1">必填</Radio>
            <Radio label="0">非必填</Radio>
          </RadioGroup>
        </FormItem>
        <FormItem label="字符串最大长度" v-if="formItem.filedType==='1'" prop="stringLength">
          <Input v-model.trim="formItem.stringLength" placeholder="请输入字符串最大长度"></Input>
        </FormItem>
        <!--整数最大值最小值-->
        <FormItem label="最小值" v-if="formItem.filedType==='2'" prop="min">
          <Input v-model.trim="formItem.min" placeholder="请输入最小值"></Input>
        </FormItem>
        <FormItem label="最大值" v-if="formItem.filedType==='2'" prop="max">
          <Input v-model.trim="formItem.max" placeholder="请输入最大值"></Input>
        </FormItem>
        <!--小数最大值最小值-->
        <FormItem label="最小值" v-if="formItem.filedType==='3'" prop="dmin">
          <Input v-model.trim="formItem.min" placeholder="请输入最小值"></Input>
        </FormItem>
        <FormItem label="最大值" v-if="formItem.filedType==='3'" prop="dmax">
          <Input v-model.trim="formItem.max" placeholder="请输入最大值"></Input>
        </FormItem>
        <FormItem label="保留小数位数" v-if="formItem.filedType==='3'" prop="decimalPoint">
          <Input v-model.trim="formItem.decimalPoint" placeholder="请输入保留小数位"></Input>
        </FormItem>
        <FormItem label="单选按钮值" v-if="formItem.filedType==='6'">
          <Input v-model.trim="formItem.rediaoType" placeholder="请输入单选按钮值，用逗号隔开"></Input>
        </FormItem>
        <FormItem label="单选下拉框值" v-if="formItem.filedType==='4'">
          <Input v-model.trim="formItem.singleSelect" placeholder="请输入单选下拉框值，用逗号隔开"></Input>
        </FormItem>
        <FormItem label="多选下拉框值" v-if="formItem.filedType==='5'">
          <Input v-model.trim="formItem.multipleSelect" placeholder="请输入多选下拉框值，用逗号隔开"></Input>
        </FormItem>
        <FormItem label="日期格式" v-if="formItem.filedType==='7'">
          <Select v-model="formItem.timeLimit" style="width:200px">
            <Option :value="1">yyyy-MM-dd HH:mm:ss</Option>
            <Option :value="2">yyyy-MM-dd</Option>
          </Select>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button @click="addField" type="primary">添加</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
export default {
  name: 'editTableAddField',
  data () {
    const validateMax = (rule, value, callback) => {
      if (this.formItem.min !== undefined && this.formItem.max !== undefined && this.formItem.min !== '' && this.formItem.max !== '') {
        if (this.formItem.min * 1 > this.formItem.max * 1) {
          callback(new Error('最大值必须大于最小值'))
          this.$refs.FormRef.validateField('min')
        } else {
          callback()
          this.$refs.FormRef.validateField('min')
        }
      } else {
        callback()
      }
    }
    const validateMin = (rule, value, callback) => {
      if (this.formItem.min !== undefined && this.formItem.max !== undefined && this.formItem.min !== '' && this.formItem.max !== '') {
        if (this.formItem.min * 1 > this.formItem.max * 1) {
          callback(new Error('最小值必须小于最大值'))
        } else {
          callback()
        }
      } else {
        callback()
      }
    }
    return {
      tableList: [], // 存储信息调查表的列表数据
      activeList: 0,
      /* 表单值 */
      formItem: {
        filedName: '',
        filedType: '1',
        isMust: '1'
      },
      /* 表单验证 */
      loginRules: {
        stringLength: [
          { pattern: /^[1-9]\d*$/, message: '该字段为整数', trigger: 'blur' }
        ],
        filedName: [
          { required: true, message: '该字段不能为空', trigger: 'blur' }
        ],
        max: [
          { pattern: /^-?[1-9]\d*$/, message: '该字段只能是整数', trigger: 'blur' },
          { validator: validateMax, trigger: 'blur' }
        ],
        min: [
          { pattern: /^-?[1-9]\d*$/, message: '该字段只能是整数', trigger: 'blur' },
          { validator: validateMin, trigger: 'blur' }
        ],
        dmax: [
          { pattern: /^-?[0-9]+([.]{1}[0-9]+){0,1}$/, message: '该字段只能是数字或小数', trigger: 'blur' },
          { validator: validateMax, trigger: 'blur' }
        ],
        dmin: [
          { pattern: /^-?[0-9]+([.]{1}[0-9]+){0,1}$/, message: '该字段只能是数字或小数', trigger: 'blur' },
          { validator: validateMin, trigger: 'blur' }
        ],
        decimalPoint: [
          { pattern: /^[1-9]\d*$/, message: '该字段为正整数', trigger: 'blur' }
        ]
      },
      /* 添加字段框状态 */
      addFieldModel: false,
      /* 约束显示类型 */
      constraint: 0,
      valid: false
    }
  },
  props: {
  },
  mounted () {
    /* 初始化添加字段参数 */
    this.delDefault()
  },
  methods: {
    /* 添加字段 */
    addField () {
      /* 处理下拉框或选择框数据 */
      let formItem = JSON.parse(JSON.stringify(this.formItem))
      if (formItem.filedType === '4') {
        formItem.selects = formItem.singleSelect.split(/[,，;；]/)
        formItem.singleSelect = formItem.selects.join(',')
      }
      if (formItem.filedType === '5') {
        formItem.selects = formItem.multipleSelect.split(/[,，;；]/)
        formItem.multipleSelect = formItem.selects.join(',')
      }
      if (formItem.filedType === '6') {
        formItem.selects = formItem.rediaoType.split(/[,，;；]/)
        formItem.rediaoType = formItem.selects.join(',')
      }
      /* 处理日期格式 */
      switch (formItem.timeLimit) {
        case 1:
          formItem.timeLimit = 'yyyy-MM-dd HH:mm:ss'
          break
        case 2:
          formItem.timeLimit = 'yyyy-MM-dd'
          break
      }

      this.handleSubmit()

      if (this.valid) {
        this.addFieldModel = false
        this.delDefault()
        this.$emit('addArr', formItem)
      }
    },
    /* 表单验证 */
    handleSubmit () {
      this.$refs['FormRef'].validate((valid) => {
        this.valid = valid
      })
    },
    /* 清空列表不同类型的附加值 */
    delAddValue () {
      let filedName = this.formItem.filedName
      let filedType = this.formItem.filedType
      this.formItem = {
        filedName: filedName,
        filedType: filedType,
        isMust: '1'
      }
      this.$refs.FormRef.resetFields()
    },
    /* 初始化表单 */
    delDefault () {
      this.formItem = {
        filedName: '',
        filedType: '1',
        isMust: '1'
      }
    }
  }
}
</script>

<style scoped lang="scss">
.content{
  padding: 15px;
}
.tablelist{
  line-height: 40px;
  &:hover{
    background: #f6f6f6;
    cursor: pointer;
  }
  &.active{
    background: #57a3f3;
    color: white;
  }
}
</style>
